    :default-active="activeIndex"
    class="el-menu-demo"
    :class="{ 'dark-theme': currentTheme === 'dark' }"
    mode="horizontal">

    <!-- Logo区域 -->
    <el-menu-item index="logo" class="logo-item">
      <img src="../assets/logo.png" alt="Logo" class="logo" />
      <span class="logo-text">Site Map</span>
    </el-menu-item>

    <!-- 首页菜单项 -->
    <el-menu-item index="home" @click="handleHome">
      <el-icon><House /></el-icon>首页
    </el-menu-item>

    <!-- 关于页面菜单项 -->
    <el-menu-item index="about" @click="$emit('show-about')">
      <el-icon><InfoFilled /></el-icon>关于
    </el-menu-item>

    <!-- 捐赠页面菜单项 -->
    <el-menu-item index="donate" @click="$emit('show-donate')">
      <el-icon><Coffee /></el-icon>捐赠
    </el-menu-item>

    <!-- 提交网站菜单项（当前隐藏） -->
    <el-menu-item index="reward" @click="$emit('show-reward')" v-show="false">
      <el-icon><Plus /></el-icon>提交网站
    </el-menu-item>

    <el-menu-item index="theme" style="margin-left: auto;" @click="$emit('toggle-theme')">
      <el-icon>
        <Sunny v-if="currentTheme === 'dark'" />
        <Moon v-else />
      </el-icon>
    </el-menu-item>
  </el-menu>
</template>

<script>
// 导入 Element Plus 图标组件
import { House, InfoFilled, Coffee, Plus, Moon, Sunny } from '@element-plus/icons-vue'

export default {
  name: 'NavigationHeader',
  // 注册使用的图标组件
  components: {
    House,
    InfoFilled,
    Coffee,
    Plus,
    Moon,
    Sunny,
  },
  // 组件属性定义
  props: {
    // 当前激活的菜单项索引
    activeIndex: {
      type: String,
      default: ''
    },
    // 当前主题（light/dark）
    currentTheme: {
      type: String,
      required: true
    }
  },
  // 定义组件可触发的事件
  emits: ['show-about', 'show-donate', 'show-reward', 'select', 'toggle-theme', 'test'],
  methods: {
    // 处理菜单项选择事件
    handleSelect(key) {
      this.$emit('select', key)
    },
    // 处理首页跳转
    handleHome() {
      window.location.href = 'http://192.168.31.81:8080/';
    }
  }
}
</script>

